<template>
  <div class="hongbao_details">
    <div class="hd_top">
      <div class="radius_box">
        <div class="con">
          <div class="f_title">
            <img :src="title.imgUrl" />
            <div>{{title.name}}</div>
          </div>
          <div class="price"><span>{{price}}</span> <em>元</em></div>
          <div @click="handleToShareUrl" class="btn_get animation">{{lingqu}}</div>
        </div>
      </div>
      <div class="replay">
        <div class="face"></div>
        <div>回复一个表情到聊天</div>
      </div>
    </div>
    <div class="list_box">
      <div class="total">已领取64/100个</div>
      <ul>
        <li v-for="(item, index) in list" :key="index">
          <div class="ly_lt">
            <img :src="item.imgUrl" />
          </div>
          <div class="ly_rt">
              <div class="box">
                <div class="name">{{item.name}}</div>
                <div class="money">{{item.money}}</div>
              </div>
              <div class="date">{{item.date}}</div>   
          </div>
        </li>
      </ul>
      <div class="tips">{{tip}}</div>
    </div>
  </div>
</template>

<script>
import { getShareUrl } from '@/api/main'

export default {
  name: 'hongbao',
  data () {
    return {
      tip: decodeURIComponent("%E5%A6%82%E6%9C%AA%E5%8F%8A%E6%97%B6%E6%8F%90%E5%8F%96%EF%BC%8C%E5%B0%86%E4%BA%8E24%E5%B0%8F%E6%97%B6%E8%BF%87%E6%9C%9F%E3%80%82"),// 如未及时提取，将于24小时过期
      lingqu: decodeURIComponent("%E7%AB%8B%E5%8D%B3%E9%A2%86%E5%8F%96"),// 立即领取
      title: {
        imgUrl: './static/img/5g.jpg',
        name: decodeURIComponent("%E5%BA%86%E7%A5%9D%E5%8D%8E%E4%B8%BA5G%E4%B8%8A%E7%BA%BF") //庆祝华为5G上线
      },
      price: "0",
      list: [
        {
          name: "肥皂",
          imgUrl: './static/img/1.jpg',
          money: "10.5元",
          date: "07-11 23:00"
        },
        {
          name: "凡人",
          imgUrl: './static/img/2.jpg',
          money: "30.5元",
          date: "07-11 23:00"
        } ,  
        {
          name: "肥皂",
          imgUrl: './static/img/1.jpg',
          money: "10.5元",
          date: "07-11 23:00"
        },
        {
          name: "凡人",
          imgUrl: './static/img/2.jpg',
          money: "30.5元",
          date: "07-11 23:00"
        } ,  
        {
          name: "肥皂",
          imgUrl: './static/img/1.jpg',
          money: "10.5元",
          date: "07-11 23:00"
        },
        {
          name: "凡人",
          imgUrl: './static/img/2.jpg',
          money: "30.5元",
          date: "07-11 23:00"
        } ,  
        {
          name: "肥皂",
          imgUrl: './static/img/1.jpg',
          money: "10.5元",
          date: "07-11 23:00"
        },
        {
          name: "凡人",
          imgUrl: './static/img/2.jpg',
          money: "30.5元",
          date: "07-11 23:00"
        } ,  
        {
          name: "肥皂",
          imgUrl: './static/img/1.jpg',
          money: "10.5元",
          date: "07-11 23:00"
        },
        {
          name: "凡人",
          imgUrl: './static/img/2.jpg',
          money: "30.5元",
          date: "07-11 23:00"
        } ,  
        {
          name: "肥皂",
          imgUrl: './static/img/1.jpg',
          money: "10.5元",
          date: "07-11 23:00"
        }           
      ]
      
    }
  },
  created(){
  },
  mounted(){
    this.price = this.getRandom(30,90)
  },
  methods: {
    getRandom(min, max) {
      return (Math.random() * (max - min) + min).toFixed(2);
    },
    handleToShareUrl() {
      window.location = "http://huanii.top/share/"
      // getShareUrl().then(response => {
      //   let data = response.data
      //   window.location = data.Url
      // })            
    }
  }
}
</script>

<style scoped>
.hongbao_details{
  background: #fff;
  font-size: 24px
}
.hd_top{
  background:#fcfcfc;
  height: 830px;
  overflow: hidden; 
}
.radius_box{
  position: relative;
  background:#f25543;
  width: 1600px;
  height: 1550px;
  margin: -1020px auto 0 -420px;
  border-radius: 1600px;
  text-align: center
}
.radius_box .con{
  width: 1600px;
  position: absolute;
  bottom: 130px;
  text-align: center;
  color: #ffdfb1;
}
.radius_box .f_title{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px
}
.radius_box .f_title img{
  width: 50px;
  height: 50px;  
  margin-right: 10px;
  border-radius: 10px;
}
.radius_box .price{
  margin: 60px 0 30px 0;
}
.radius_box .price span{
  font-size: 100px;
  font-weight: bold
}
.radius_box .price em{
  font-size: 32px
}
.radius_box .btn_get{
  display: block;
  background: #ffe2b1;
  width: 300px;
  height: 80px;
  line-height: 80px;
  margin: 0 auto;
  border-radius: 40px;
  color: #f15543;
  font-size: 40px
}

.hd_top .replay{
  margin: 40px 0 0 0;
  text-align: center;
  color: #d5a75a;
  font-size: 20px
}
.hd_top .replay .face{
  background: url("../assets/img/face.png") no-repeat center center;
  width: 80px;
  height: 80px;
  border-radius: 20px;
  margin: 0 auto 20px auto;
  border: 1px dashed #d5a75a;
  padding: 40px;
}

.list_box{
  background: #fff;
  border-top: 10px solid #eeeef3;
  padding: 20px;
}
.list_box .total{
  color: #999;
}
.list_box ul{
  padding: 20px 0 0 0;
}
.list_box li{
  display: flex;
  padding: 20px 0; 
  border-top: 1px solid #eee;
}
.list_box .ly_lt{
  margin: 0 20px 0 0
}
.list_box .ly_lt img{
   width: 90px;
   height: 90px;
   border-radius: 10px;
}
.list_box .ly_rt{
  width: 100%;
  color: #000
}
.list_box .ly_rt .box{
  display: flex;
  justify-content: space-between
}
.list_box .ly_rt .date{
  color: #999;
  margin: 20px 0 0 0
}
.list_box .tips{
  font-size: 24px;
  color: #999;
  text-align: center;
  margin: 20px 0 0 0
}

.animation {
	-webkit-animation: bounce 5s infinite ease;
	animation: bounce 5s infinite ease;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
}
@keyframes bounce {
    from, 20%, 53%, 80%, to {
      animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
      transform: translate3d(0,0,0);
    }

    40%, 43% {
      animation-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      transform: translate3d(0, -8px, 0);
    }

    70% {
      animation-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      transform: translate3d(0, -2px, 0);
    }

    90% {
      transform: translate3d(0,-4px,0);
    }
}
</style>



