<template>

  <mescroll-vue ref="mescroll" :down="mescrollDown" >
      <div class="open_details">
        <div v-if="isDetailsPage">
          <div class="box animation">
            <div class="txt"></div>
            <div @click="goLand" class="btn"></div>
          </div>
        </div>

        <transition  name="fade-transform" mode="out-in">
          <hongbao-Land v-if="isDetailsPage == false"/>
        </transition>
      </div>
  </mescroll-vue>

</template>

<script>
import MescrollVue from 'mescroll.js/mescroll.vue'
import hongbaoLand from '@/components/hongbao'

export default {
  name: 'open',
  components: {
    hongbaoLand,
    MescrollVue
  },
  data () {
    return {
      isDetailsPage: true,
      mescroll: null, 
      mescrollDown:{
        callback: this.downCallback,
        textInOffset: decodeURIComponent("%3Cdiv%20style%3D%22font-size%3A%2012px%3B%20color%3A%20%23ccc%22%3E%E6%AD%A4%E7%BD%91%E9%A1%B5%E7%94%B1%20mp.weixin.qq.com%20%E6%8F%90%E4%BE%9B%3C%2Fdiv%3E%3Cbr%2F%3E%3Cbr%2F%3E'"), // 下拉的距离在offset范围内的提示文本
        textOutOffset: decodeURIComponent("%3Cdiv%20style%3D%22font-size%3A%2012px%3B%20color%3A%20%23ccc%22%3E%E6%AD%A4%E7%BD%91%E9%A1%B5%E7%94%B1%20mp.weixin.qq.com%20%E6%8F%90%E4%BE%9B%3C%2Fdiv%3E%3Cbr%2F%3E%3Cbr%2F%3E'"), // 下拉的距离大于offset范围的提示文本
        htmlContent: '<p class=""></p><p class="downwarp-tip"></p>', // 布局内容
        textLoading: '', // 加载中的提示文本
      }
    }
  },
  created(){
  },
  mounted(){

  },
  methods: {
    downCallback (mescroll) {
      this.$nextTick(() => {
			  mescroll.endSuccess()
			})      
    },
    goLand(){
      this.isDetailsPage = false
    }
  }
}
</script>

<style scoped>
.mescroll {
  position: fixed;
  top: 0;
  bottom: 0;
  height: auto;
}
.open_details{
  background: #eee;
  width: 100%;
  height: 100%;
  font-size: 24px;
  color: #e6cea1;
}
.open_details .box{
  position: fixed;
  top: 50%;
  left: 50%;
  width: 600px;
  height: calc(90vh - 200px); 
  /* min-height: calc(90vh - 50px); */
  margin: calc(-46vh + 100px) auto 0 -300px;
  background: #f35d4e url(../assets/img/open_bg.jpg) no-repeat  center bottom;
  background-size: 100%;
  border-radius: 20px; 
  /* box-shadow: 0 0 10px #888;   */
}
.open_details .txt{
  width: 531px;
  height: 468px; 
  margin: 0 auto;
  background: url(../assets/img/txt.png) no-repeat center center;
  background-size: 100%;
  padding-bottom: 100px;
}
.open_details .f_title{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px
}
.open_details .f_title img{
  width: 50px;
  height: 50px;  
  margin-right: 10px;
  border-radius: 10px;
}
.btn{
  position: absolute;
  width: 270px;
  height: 270px; 
  margin: 0 auto 0 -135px;
  bottom: 30px;
  left: 50%;
}

.animation {
	-webkit-animation: twinkling 5.1s infinite ease;
	animation: twinkling 5.1s infinite ease;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
}
@-webkit-keyframes twinkling {
	0% {
    -webkit-transform:scale(1);
  }
  50% {
    -webkit-transform:scale(1.1);
  }
  100% {
    -webkit-transform:scale(1);
  }
}
@keyframes twinkling {
	0% {
    -webkit-transform:scale(1);
  }
  50% {
    -webkit-transform:scale(1.1);
  }
  100% {
    -webkit-transform:scale(1);
  }
}

/*fade-transform*/
.fade-transform-leave-active,
.fade-transform-enter-active {
  transition: all .28s;
}
.fade-transform-enter {
  opacity: 0;
  transform: translateX(230px);
}
.fade-transform-leave-to {
  opacity: 0;
  transform: translateX(-30px);
}

</style>



